<template>
  <div>
    <!--<base-table-->
      <!--:columns="columns"-->
      <!--:data="mergeData"-->
      <!--:show-header="true"-->
      <!--row-key="id"-->
      <!--@edit="edit"-->
      <!--@approval="approval"-->
      <!--:spanMethod="merge"-->
      <!--:showSummary="true"-->
      <!--:summaryMethod="null"-->
      <!--:mountedCallBack="tick"-->
      <!--:pagination="true"-->
      <!--:total="130">-->
    <!--</base-table>-->
    <base-table
      :columns="columns1"
      :data="stuData"
      :pagination="true"
      :total="data1.length"
      rowKey="no"
      @dataChange="dataChange"
      @selectChange="selectChange">
    </base-table>
    <el-button type="primary" :loading="true">加载中</el-button>
  </div>
</template>

<script>
import {tableDataMerge} from 'utils/commonUtil'
const BaseTable = () => import('@/components/BaseTable')

export default {
  name: 'index',
  components: {BaseTable},
  data () {
    return {
      columns: [
        {prop: 'bizSector', width: 120, label: '业务板块', align: 'center'},
        {prop: 'bizClassNo', width: 120, label: '业务小类号', align: 'center'},
        {prop: 'premiumScale', width: 120, label: '保费规模', align: 'center', isEdit: true, type: 'input'},
        {
          prop: 'premiumAmount',
          width: 120,
          label: '保费收入占比',
          align: 'center',
          children: [
            {prop: 'forecastValue', width: 120, label: '预测值', align: 'center'},
            {prop: 'realValue', width: 120, label: '实际值', align: 'center', isEdit: true, type: 'input'},
            {prop: 'diff', width: 120, label: '二者差', align: 'center', isEdit: true, type: 'select', options: [{value: 'test', label: '测试'}]}
          ]
        },
        {prop: 'status', width: 120, label: '状态', align: 'center', status: true},
        {prop: 'operation', width: 120, label: '操作', align: 'center', operation: true, operations: [{text: '编辑', event: 'edit'}, {text: '审批', event: 'approval'}]}
      ],
      data: [
        {
          id: 'hn-1',
          bizSector: '家用-新车',
          bizClassNo: '1',
          premiumScale: '8.89',
          forecastValue: '51.00',
          realValue: '61.01',
          diff: '10.01',
          status: '审批',
          statusType: ''
        },
        {
          id: 'hn-2',
          bizSector: '家用-新车',
          bizClassNo: '1',
          premiumScale: '8.89',
          forecastValue: '51.00',
          realValue: '61.01',
          diff: '10.01',
          status: '待审批',
          statusType: 'success'
        },
        {
          id: 'ho-1',
          bizSector: '家用-旧车',
          bizClassNo: '1',
          premiumScale: '10.88',
          forecastValue: '51.00',
          realValue: '61.01',
          diff: '10.01',
          status: '已审批',
          statusType: 'warn'
        },
        {
          id: 'ho-2',
          bizSector: '家用-旧车',
          bizClassNo: '2',
          premiumScale: '10.88',
          forecastValue: '51.00',
          realValue: '61.01',
          diff: '10.01',
          status: '审批',
          statusType: 'danger'
        }
      ],
      columns1: [
        {prop: 'index', width: 50, label: '', type: 'selection'},
        {prop: 'no', width: 120, label: '编号'},
        {prop: 'name', width: 120, label: '姓名'}
      ],
      data1: [
        {no: '1', name: '赵'},
        {no: '2', name: '钱'},
        {no: '3', name: '孙'},
        {no: '4', name: '李'},
        {no: '5', name: '周'},
        {no: '6', name: '吴'},
        {no: '7', name: '郑'},
        {no: '8', name: '王'},
        {no: '9', name: '冯'},
        {no: '10', name: '陈'},
        {no: '11', name: '褚'},
        {no: '12', name: '卫'}
      ],
      pageSize: 5,
      currentPage: 1
    }
  },
  computed: {
    mergeData () {
      const props = ['bizSector', 'bizClassNo']
      return tableDataMerge(this.data, props)
    },
    stuData () {
      const result = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end > this.data1.length ? this.data1.length : end
      for (let i = start; i < end; ++i) {
        result.push(this.data1[i])
      }
      return result
    }
  },
  methods: {
    tick () {
      const footer = document.getElementsByClassName('el-table__footer')[0]
      const tbody = footer.getElementsByTagName('tbody')[0]
      const tr = tbody.getElementsByTagName('tr')[0]
      const tds = tr.getElementsByTagName('td')
      tr.removeChild(tds[1])
      tds[0].setAttribute('colspan', '2')
    },
    edit (id) {
      console.log('edit', id)
    },
    approval (id) {
      console.log('approval', id)
    },
    merge ({row, column, rowIndex, columnIndex}) {
      const props = ['bizSector', 'bizClassNo']
      if (props.includes(column.property)) {
        if (row.rowSpan[column.property] > 1) {
          return [row.rowSpan[column.property], 1]
        } else if (row.rowSpan[column.property] === 0) {
          return [0, 0]
        }
      }
    },
    dataChange (size, page) {
      this.pageSize = size
      this.currentPage = page
    },
    selectChange (keys) {
      console.log(keys)
    }
  }
}
</script>

<style scoped>

</style>



